.city {
    margin: auto;
    border-radius: 15px;
    width: 70%;
    border:  1px solid #b98c00;
    -webkit-box-shadow: 0 5px 6px #cccccc;
            box-shadow: 0 5px 6px #cccccc;
    padding: 16px;
    background-color: #FF922B;
    background: -webkit-gradient(linear, left top, left bottom, from(#FF9F44), to(#FF922B));
    background: -webkit-linear-gradient(#FF9F44, #FF922B);
    background: -o-linear-gradient(#FF9F44, #FF922B);
    background: linear-gradient(#FF9F44, #FF922B);
    text-align: center;

}

#cityText{
    width: 30px;
    font-size: 10px;
    overflow-x: hidden; /* Hide horizontal scrollbar */
    overflow-y: scroll; /* Add vertical scrollbar */
}

#extraInfo{
    padding-left: 0;

}
li{
    display: inline-block;
    width: 20%;
}


